*{
  box-sizing:border-box;
}
body{
  font:normal 14px Arial,sans-serif;
}
.contact-page{
  max-width:500px;
  margin:0 auto;
}
h1{
  text-align:center;
  font-weight:500;
}
input[type=email],input[type=text],select,textarea{
  width:100%;
  padding:12px;
  border:1px solid #ccc;
  border-radius:4px;
  box-sizing:border-box;
  margin-top:6px;
  margin-bottom:16px;
  resize:vertical;
}
input[type=submit]{
  background-color:#4caf50;
  color:#fff;
  padding:12px 20px;
  border:none;
  border-radius:4px;
  cursor:pointer;
}
fieldset{
  background-color:#eee;
}
label{
  font:normal 14px Arial,sans-serif;
}
input[type=submit]:hover{
  background-color:#45a049;
}
.container{
  border-radius:5px;
  background-color:#f2f2f2;
  padding:20px;
}
